import { CustomRenderComponent } from './../../core/render/custom-render/custom-render.component';
import { TimeManagementService } from './../../core/service/time-management/time-management.service';
import { SubSink } from 'subsink';
import { idRender } from './../../core/interface/common.interface';
import { Router } from '@angular/router';
import { tableOption, ApiStatus, tableConfig, timeSheetFilter, dateFilter } from './../../core/constants/constants';
import { BadgeComponent } from './../../widget/badge/badge.component';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { IdRenderComponent } from 'src/app/core/render/id-render/id-render.component';

@Component ({
  selector: 'app-time-management-landing-page',
  templateUrl: './time-management-landing-page.component.html',
  styleUrls: ['./time-management-landing-page.component.scss']
})
export class TimeManagementLandingPageComponent implements OnInit , OnDestroy {
  public tableLimitOption = tableConfig.tableLimitOption;

  public tableLimit = tableConfig.tableLimit;

  public tableSearch = '';

  public currentPage = 1;

  public gridData:[];

  public totalCount = 0;

  public itemsPerPage = tableConfig.pager;

  public tableOption = tableOption;

  public timeSheetFilter = timeSheetFilter;

  public activeTimeSheetFilter = 0;

  public tableSettings = {
    column: {
      id: {
        title: 'Id',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'custom',
        renderComponent: IdRenderComponent,
        valuePrepareFunction: (value :idRender, row) => {
          value = {
              id: row?.id ,
              icon: 'ri-more-2-fill',
              color: '#3298DB',
              event: 'view',
              row: row,
              route: `/time-management/detail/${row?.id}`
          };
          return value;
        }
      },
      wbselementname: {
        title: 'WBS Element Name',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },

      wbsshortdescription: {
        title: 'WBS Short Description',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },

      start_date: {
        title: 'Start Date',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      start_time: {
        title: 'Start Time',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      end_time: {
        title: 'End Time',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      end_date: {
        title: 'End Date',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      assignee: {
        title: 'Assignee',
        icon: '',
        class: 'pointer',
        type: 'custom',
        renderComponent: CustomRenderComponent,
        valuePrepareFunction: (value,row) => {
          return row?.assignee?.name;
        }
      },
      ApprovalStatus: {
        title: 'Status',
        type: 'custom',
        renderComponent: BadgeComponent,
        valuePrepareFunction: (value, row) => {
          if (row) {
            if (row.status=== 'APPROVED') {
              value = {
                color: '#9DE10A',
                value: row.status,
                fontColor: 'white'
              };
            }  else  if (row.status === 'CANCELLED') {
              value = {
                color: '#ff5252',
                value: row.status,
                fontColor: 'white'
              };

            } else  if (row.status === 'REJECTED'){
              value = {
                color: '#e00404',
                value: row.status,
                fontColor: 'white'
              };
            }   else  if (row.status === 'PENDING'){
              value = {
                color: '#f77300',
                value: row.status,
                fontColor: 'white'
              };
            } else {
              value = {
                color: '',
                value: row.status,
                fontColor: '#000000'
              };
            }

          }
          return value;
        }
      },
  }
  }


public  stackedChartConfiguration = {
    width: 400,
    height: 320,
    margin: { top: 40, right: 40, bottom: 40, left: 40 },
    offsetHeight: 40
  }

 public employeeChartConfiguration = {
    width: 300,
    height: 200,
    margin: { top: 40, right: 40, bottom: 40, left: 40 },
    offsetHeight: 40
  }

public approvalStatus = [];

public approvalCount : number;

public departmentChart = {
  color: [],
   label: [],
    data: [],
};

public dateFilter = dateFilter;

public selectedDateFilter = null;

public timeChart = {};

private subs = new SubSink ();

  constructor( private _router: Router ,  private _timeService: TimeManagementService
    ) { }

ngOnInit(): void {
    this.gridData = [];
    this.setChart ();
    this.timeDetails ();
  }

  onTimesheetFilterClick(event) {
    this.activeTimeSheetFilter = event.index;
  }



setChart() {
    this.subs.add (
      this._timeService.chart ( ).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          this.approvalStatus = res.timesheet?.time_entry_approval_status?.data;
          this.approvalCount = res.timesheet?.time_entry_approval_status?.count;
         this.departmentChart = this.horizontal ( res.timesheet?.time_entry_by_department);
        //  this. timeChart = this.horizontal(res.employeeHorizontal);
        }
      })
    );
  }

  horizontal(res) {
    const color = [];
    const label = [];
    const data = [];
    res.forEach ((element, i) => {
      label.push (element.type);
      element.details.forEach ((el, j) => {
        if (!data[j]) {
          data[j] = { name: '', data: [] };
        }
        const label = data[j].name = el.label;
        data[j].data.push (el.value);
        color[j] = el.color;
      });
    });
   return {color,
   label,
    data };
  }

  tablePageChange(offSet: number) {
    this.timeDetails ( offSet , this.tableSearch, this.tableLimit);
  }

 timeDetails( offSet = 1, search = this.tableSearch, limit = this.tableLimit) {
    const gridParams = {
      q: search,
      offset: (offSet - 1) * this.tableLimit,
      limit: limit,
    };
    this.subs.add (
      this._timeService.grid ( gridParams).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          this.gridData = res.results;
          this.totalCount =  res.count;
        }
      })
    );
  }

  search() {
    this.currentPage = 1;
    this.timeDetails ();
  }

  dataFilterClick(event) {
    this.selectedDateFilter = event.index;
  }

  create() {
    this._router.navigate (['/task-entry/create/time']);
  }

  ngOnDestroy() {
    this.subs.unsubscribe ();
  }
}
