import { TimeManagemnetDetailComponent } from './time-managemnet-detail/time-managemnet-detail.component';
import { TimeManagementLandingPageComponent } from './time-management-landing-page/time-management-landing-page.component';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

const routes: Routes = [{
  path: '',
  component: TimeManagementLandingPageComponent,
  data: {
    title: 'Time Approval',
    mainMenu: 'Time Approval',
    hideTitle: true,
    urls: []
  }
},
{
  path: 'detail/:id',
  component: TimeManagemnetDetailComponent,
  data: {
    title: 'Time Approval',
    mainMenu: 'Time Approval',
    hideTitle: true,
    urls: []
  }
},
];

@NgModule ({
  imports: [RouterModule.forChild (routes)],
  exports: [RouterModule]
})
export class TimeManagementRoutingModule { }
