import { ToastService } from 'src/app/core/helper/toast.helper.service';
import { Status } from './../../core/interface/common.interface';
import { SubSink } from 'subsink';
import { TimeManagementService } from './../../core/service/time-management/time-management.service';
import { ApiStatus } from 'src/app/core/constants/constants';
import { Router, ActivatedRoute } from '@angular/router';
import { Component, OnInit, OnDestroy } from '@angular/core';

@Component ({
  selector: 'app-time-managemnet-detail',
  templateUrl: './time-managemnet-detail.component.html',
  styleUrls: ['./time-managemnet-detail.component.scss']
})
export class TimeManagemnetDetailComponent implements OnInit , OnDestroy {

  private subs = new SubSink ();

  public timeDetails: TimeDetails;

  public comment:string;

  private id: string;

  public userList: string[];

  constructor(
     private _router: Router,
     private _timeService: TimeManagementService,
     private _toast: ToastService,
     private _activated: ActivatedRoute) { }

ngOnInit(): void {
  this.comment = '';
 this.getUserList ();
  this.subs.add (
    this._activated.params.subscribe (res => {
      this.id = res['id'];
      if (this.id) {
        this.getDetail ();
      }
    })
  );
  }

  view(){
      this._router.navigate (['/time-management']);
  }

  getUserList() {
    this.subs.add (
      this._timeService.userList ()
        .subscribe (data => {
          if (data.status === ApiStatus.ok) {
            this.userList = [];
            data.forEach (element => {
              this.userList.push (element.name);
            });
          }
        })
    );
  }

  getDetail () {
    this.subs.add (
      this._timeService.timeDetails (this.id ).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          delete res.Status;
          this.timeDetails = res[0];
      }
}
    ));
  }

  approve () {
    const approve: any = new FormData ();
    approve.append ('request_id', this.timeDetails?.approval_level_set[0]?.level1?.request_id);
    approve.append ('time_id', + this.id);
    approve.append ('action_type',  'approve');
    approve.append ('request_type', 'create');
    approve.append ('comment', this.comment);

    this.subs.add (
      this._timeService.approve (approve ).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          this._toast.success (res.message);
          this.view ();
      }
}
    ));
  }

  reject() {
    const reject: any = new FormData ();
    reject.append ('request_id', this.timeDetails?.approval_level_set[0]?.level1?.request_id);
    reject.append ('time_id', + this.id);
    reject.append ('action_type',  'reject');
    reject.append ('request_type', 'create');
    reject.append ('comment', this.comment);
    this.subs.add (
      this._timeService.approve (reject ).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          this._toast.success (res.message);
          this.view ();
      }
}
    ));
  }

  ngOnDestroy() {
    this.subs.unsubscribe ();
  }
}
interface TimeDetails {
  id: number;
  deleted_at?: any;
  created_date?: any;
  modified_date?: any;
  wbselementname: string;
  wbsshortdescription?: any;
  comment: string;
  start_date: string;
  end_date: string;
  status: string;
  start_time: string;
  end_time: string;
  created_by?: any;
  updated_by?: any;
  workflow: string;
  assignee: {id:string,name:string}
  CompanyCode: {value:  string;},
  approval_level_set : approvalLevelSet[]
}
interface approvalLevelSet {
  level1: Level1;
}

interface Level1 {
  request_id: number;
  level: number;
  is_active: boolean;
  closed: boolean;
  request_type: string;
  approver: string;
}
